<template>
  <div class="player" v-show="playlist.length>0">
    <!--  -->
    <transition class="animated" :enter-active-class="enterAnmiate[randomNum(0, 17)]" :leave-active-class="leaveAnmiate[randomNum(0, 14)]">
      <div class="normal-player" v-show="fullScreen">
        <div class="normal-bg">
          <img :src="currentSong.al.picUrl" alt="">
        </div>
        <div class="header">
          <img src="./toDown.png" alt="" title="" @click="toDown">
          <h1 v-html="currentSong.name"></h1>
        </div>
        <h1 class="title-singer" v-html="currentSong.ar[0].name"></h1>
        <!--  -->
        <div class="middle" @touchstart.prevent="middleTouchStart" @touchmove.prevent="middleTouchMove" @touchend="middleTouchEnd">
          <img :class="playImg" :src="currentSong.al.picUrl" alt="">
          <p :class="playImgTxt" class="cd-lyric">
            {{playingLyric}}
          </p>
          <scroll class="middle-r middleTime" ref="lyricList" :data="currentLyric && currentLyric.lines">
            <div class="lyric-wrapper">
              <div v-if="currentLyric">
                <p ref="lyricLine" class="text" :class="{'current': currentLineNum ===index}" v-for="(line, index) in currentLyric.lines" :key="index">{{line.txt}}</p>
              </div>
            </div>
          </scroll>
        </div>
        <!--  -->
        <div class="dot-wrapper">
          <span class="dot" :class="{'active':currentShow==='cd'}"></span>
          <span class="dot" :class="{'active':currentShow==='lyric'}"></span>
        </div>
        <!--  -->
        <div class="time-box">
          <div class="time">
            <span class="time-l">{{ format(currentTime) }}</span>
            <progressBar style="overflow:hidden" :percent="percent" @percentChange="percentChange"></progressBar>
            <span class="time-r">{{ format(duration) }}</span>
          </div>
        </div>
        <!--  -->
        <div class="bottom-button-box">
          <span @click="changeMode">
            <img v-if="mode === 2" src="./btnImg/random.png" alt="">
            <img v-if="mode === 0" src="./btnImg/sequence.png" alt="">
            <img v-if="mode === 1" src="./btnImg/loop.png" alt="">
          </span>
          <span @click="prev"><img src="./btnImg/prve.png" alt=""></span>
          <span @click="togglePlaying">
            <img v-if="playing" id="playButton" src="./playStrop.png" alt="">
            <img v-if="!playing" id="playButton" src="./playButton.png" alt="">
          </span>
          <span @click="next"><img src="./btnImg/next.png" alt=""></span>
          <span @click="toggleFavoriteList(currentSong)">
            <img v-if="getFavoriteListCollect(currentSong)" src="./btnImg/collect.png" alt="">
            <img v-else src="./btnImg/collect2.png" alt="">
          </span>
        </div>
        <!--  -->
        <audio ref="audio" id="audioPlay" @canplay="audioReady" @error="audioError" @timeupdate="timeUpdate" @ended="songEnd"></audio>
      </div>
    </transition>
    <!--  -->
    <div class="mini-player" v-show="!fullScreen" @click="toUp">
      <div class="mini-player-con">
        <img :class="playing? 'playSrart' : 'playStorp'" :src="currentSong.al.picUrl" alt="">
        <p>
          <span class="mini-title" v-html="currentSong.name"></span>
          <span class="mini-name" v-html="currentSong.ar[0].name"></span>
        </p>
        <div class="playButton-box">
          <span @click.stop="togglePlaying" class="playButton">
            <img v-if="playing" src="./playStrop.png" alt="">
            <img v-if="!playing" src="./playButton.png" alt="">
          </span>
          <span class="playList" @click.stop="playListClick">
            <img src="./playList.png" alt="" title="">
          </span>
        </div>
      </div>
    </div>
    <!--  -->
    <playList ref="playList"></playList>
  </div>
</template>
<script>
import { mapGetters, mapMutations, mapActions } from "vuex";
// import { songUrl } from "assets/js/song";
import { shuffle } from "assets/js/util";
import { getVkey, getLyric } from "api/song";
// import { ERR_OK } from "api/config";
import progressBar from "./components/progress-bar";
import playList from "components/playList/playList";
import { playMode } from "assets/js/config";
// import { Base64 } from "js-base64";
import Lyric from "lyric-parser";
import Scroll from "base/scroll/scroll";
const ERR_OK = 200;

export default {
  components: {
    progressBar,
    Scroll,
    playList
  },
  computed: {
    ...mapGetters([
      "fullScreen",
      "playlist",
      "currentSong",
      "vkey",
      "playing",
      "currentIndex",
      "mode",
      "sequenceList",
      "favoriteList" // 
    ]),
    percent() {
      return this.currentTime / this.duration;
    },
    playImg() {
      let class1 = this.playing ? "playSrart" : "playStorp";
      let class2 = this.currentShow === "lyric" ? "playImg" : "";
      return `${class1} ${class2}`;
    },
    playImgTxt() {
      let class2 = this.currentShow === "lyric" ? "playImg" : "";
      return `${class2}`;
    }
  },
  data() {
    return {
      songUrlData: "",
      vkeyData: "",
      songReadey: false, // 
      currentTime: "",
      currentLyric: null, // 
      currentShow: "cd", // 
      currentLineNum: 0, // 
      playingLyric: "", // cd
      duration: '',
      enterAnmiate: ['animated bounce', 'animated pulse', 'animated rubberBand', 'animated shake', 'animated swing', 'animated tada', 'animated wobble', 'animated bounceIn', 'animated bounceInDown', 'animated bounceInLeft', 'animated bounceInRight', 'animated bounceInUp', 'animated flip', 'animated flipInX', 'animated flipInX', 'animated lightSpeedIn', 'animated rollIn'],
      leaveAnmiate: ['animated bounceOut', 'animated bounceOutDown', 'animated bounceOutLeft', 'animated bounceOutRight', 'animated bounceOutUp', 'animated hinge', 'animated rollOut', 'animated rotateOutDownLeft', 'animated slideOutLeft', 'animated slideOutRight', 'animated fadeOutLeftBig', 'animated fadeOutRightBig', 'animated fadeOutDownBig', 'animated rotateOutDownRight']
    };
  },
  created() {
    this.touch = {};
    // var myaudio = document.getElementById('audioPlay');
    // var time = parseInt(myaudio.currentTime);
    // var timelength = myaudio.duration;
    // this.duration = timelength;
  },
  mounted() {
    // this._songUrl();
  },
  methods: {
    // 
    randomNum(start, end) {
      return Math.floor(Math.random() * (end - start) + start);
    },
    // 
    getFavoriteListCollect(song) {
      if (this.isFavoriteList(song)) {
        return false;
      } else {
        return true;
      }
    },
    // 
    toggleFavoriteList(song) {
      console.log(song);
      if (this.isFavoriteList(song)) {
        this.deleteFavoriteList(song);
      } else {
        this.saveFavoriteList(song);
      }
    },
    // 
    isFavoriteList(song) {
      const index = this.favoriteList.findIndex(item => {
        return item.id === song.id;
      });
      return index > -1;
    },
    /*  */
    playListClick() {
      this.$refs.playList.show();
    },
    /*  */
    togglePlaying() {
      // /
      if (this.currentLyric) {
        this.currentLyric.togglePlay();
      }
      this.setPlayingState(!this.playing);
    },
    toDown() {
      this.setFullScreen(false);
    },
    toUp() {
      this.setFullScreen(true);
    },
    /*  */
    next() {
      if (!this.songReadey) {
        return;
      }
      if (this.playlist.length === 1) {
        this.songLoop();
      } else {
        let index = this.currentIndex + 1;
        if (index === this.playlist.length) {
          index = 0;
        }
        this.setCurrentIndex(index);
        this.setPlayingState("true"); // 
      }
      this.songReadey = false;
    },
    prev() {
      if (!this.songReadey) {
        return;
      }
      if (this.playlist.length === 1) {
        this.songLoop();
      } else {
        let index = this.currentIndex - 1;
        if (index === -1) {
          index = this.playlist.length - 1;
        }
        this.setCurrentIndex(index);
        this.setPlayingState("false");
      }
      this.songReadey = false;
    },
    /*  */
    audioReady() {
      this.songReadey = true; // 
      this.savePlayHistory(this.currentSong); // 
    },
    audioError() {
      console.log("");
      this.songReadey = true; // 
    },
    /*  */
    timeUpdate(e) {
      this.currentTime = e.target.currentTime;
      // console.log(this.currentTime)
    },
    /*  */
    percentChange(precent) {
      const currentTime = this.duration * precent;
      this.$refs.audio.currentTime = currentTime;
      // 
      if (this.currentLyric) {
        this.currentLyric.seek(currentTime * 1000);
      }
    },
    /*  */
    format(interval) {
      interval = interval | 0;
      var min = (interval / 60) | 0;
      var sec = interval % 60;
      if (sec.toString().length === 1) {
        sec = `0${sec}`;
      }
      return `${min}:${sec}`;
    },
    /*  */
    changeMode() {
      const mode = (this.mode + 1) % 3; // 
      this.setPlayMode(mode);
      let list = null;
      if (mode === playMode.random) {
        list = shuffle(this.sequenceList);
      } else {
        list = this.sequenceList;
      }
      this.resetCurrentIndex(list);
      this.setPlayList(list);
    },
    resetCurrentIndex(list) {
      let index = list.findIndex(item => {
        return item.id === this.currentSong.id;
      });
      this.setCurrentIndex(index);
    },
    /*  */
    songEnd() {
      if (this.mode === playMode.loop) {
        this.songLoop();
      } else {
        this.next();
      }
    },
    /*  */
    songLoop() {
      // 
      if (this.currentLyric) {
        this.currentLyric.seek(0);
      }
      this.$refs.audio.currentTime = 0;
      this.$refs.audio.play();
    },
    /* key */
    _getVkey(mid) {
      var that = this;
      getVkey(mid).then(res => {
        console.log(res);
        if (res.code === ERR_OK) {
          let vkey = res.data[0].url;
          that.setVkey(vkey); // keyvuex
        } else {
          console.log("player vkey");
        }
      });
      /*  */
      getLyric(mid).then(res => {
        this.currentLyric = null;
        if (res.code === ERR_OK) {
          console.log(res);
          // let lyric = Base64.decode(res.lrc.lyric);
          let lyric = res.lrc.lyric;

          console.log(lyric);
          this.currentLyric = new Lyric(lyric, this.handleLyric);
          if (this.playing) {
            // 
            this.currentLyric.play();
          }
        } else {
          console.log("player Lyric");
          this.currentLyric = null;
          this.playingLyric = "";
          this.currentLineNum = 0;
        }
      });
    },
    handleLyric({ lineNum, txt }) {
      this.currentLineNum = lineNum;
      if (lineNum > 5) {
        let lineEl = this.$refs.lyricLine[lineNum - 5];
        this.$refs.lyricList.scrollToElement(lineEl, 1000);
      } else {
        this.$refs.lyricList.scrollTo(0, 0, 1000);
      }
      this.playingLyric = txt;
    },
    /*  */
    songPlay() {
      console.log(this.$refs.audio);
      console.log(this.songUrlData);
      this.$refs.audio.src = this.songUrlData;
      this.$refs.audio.crossOrigin = this.anonymous;
      this.$refs.audio.play();
      var myaudio = document.getElementById('audioPlay');
      setTimeout(() => {
        // var time = parseInt(myaudio.currentTime);
        var timelength = myaudio.duration;
        this.duration = timelength;
      }, 200)
    },
    /*  */
    middleTouchStart(e) {
      this.touch.initiated = true;
      // 
      this.touch.moved = false;
      const touch = e.touches[0];
      this.touch.startX = touch.pageX;
      this.touch.startY = touch.pageY;
    },
    middleTouchMove(e) {
      if (!this.touch.initiated) {
        return;
      }
      const touch = e.touches[0];
      const deltaX = touch.pageX - this.touch.startX;
      const deltaY = touch.pageY - this.touch.startY;
      if (Math.abs(deltaY) > Math.abs(deltaX)) {
        // 
        return;
      }
      if (!this.touch.moved) {
        this.touch.moved = true;
      }
      /*  */
      if (deltaX > 0) {
        // //
        if (this.currentShow === "lyric") {
          this.touch.toChange = Math.abs(deltaX) > 40 ? "NO" : "OK";
        } else {
          return "";
        }
      } else {
        // //CD
        if (this.currentShow === "cd") {
          this.touch.toChange = Math.abs(deltaX) > 40 ? "OK" : "NO"; // 
        } else {
          return "";
        }
      }
    },
    middleTouchEnd() {
      if (!this.touch.moved) {
        return;
      }
      if (this.touch.toChange === "OK") {
        this.$refs.lyricList.$el.style.transform = `translate3d(0px,0,0)`;
        this.currentShow = "lyric";
      } else {
        this.$refs.lyricList.$el.style.transform = `translate3d(375px,0,0)`;
        this.currentShow = "cd";
      }
    },
    ...mapMutations({
      setFullScreen: "SET_FULL_SCREEN", // 
      setPlayingState: "SET_PLAYING_STATE", // 
      setCurrentIndex: "SET_CURRENT_INDEX", // 
      setVkey: "SET_VKEY",
      setPlayMode: "SET_PLAY_MODE",
      setPlayList: "SET_PLAYLIST"
    }),
    ...mapActions(["savePlayHistory", "saveFavoriteList", "deleteFavoriteList"])
  },
  watch: {
    vkey: function(val, oldVal) {
      // this.songUrlData = songUrl(val, this.currentSong.mid);
      this.songUrlData = val;
      console.log(this.songUrlData);
      this.songPlay();
      // console.log(this.currentSong.mid)
    },
    // 
    currentSong(newSong, oldSong) {
      if (!newSong.id) {
        // newSong
        return;
      }
      if (newSong === oldSong) {
        // currentSong
        return;
      }
      // 
      console.log(newSong);
      if (this.currentLyric) {
        this.currentLyric.stop();
      }
      this.$nextTick(() => {
        this._getVkey(this.currentSong.id);
      });
    },
    // 
    playing(val) {
      this.$nextTick(() => {
        const audio = this.$refs.audio;
        val ? audio.play() : audio.pause();
      });
    }
  }
};
</script>
<style lang="scss" scoped>
@import "assets/css/mixin.scss";
.normal-player {
  color: #fff;
  z-index: 400;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgb(34, 34, 34);
}
.normal-bg {
  z-index: -1;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0.6;
  filter: blur(20px);
}
.normal-bg img {
  width: 100%;
  height: 100%;
}
.mini-player {
  display: flex;
  align-items: center;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 410;
  width: 100%;
  height: rem(60);
  background: rgb(51, 51, 51);
  padding: rem(10) rem(10) rem(10) rem(20);
  box-sizing: border-box;
}
.mini-player-con {
  width: 100%;
  height: rem(40);
}
.mini-player-con img {
  width: rem(40);
  height: rem(40);
  border-radius: rem(40);
  margin-right: rem(5);
  vertical-align: middle;
}
.mini-player-con p {
  display: inline-block;
  max-width: rem(170);
  height: rem(40);
  overflow: hidden;
  vertical-align: middle;
}
.mini-title {
  display: block;
  font-size: rem(14);
  height: rem(20);
  color: #fff;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: rem(20);
}
.mini-name {
  display: block;
  font-size: rem(12);
  height: rem(20);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: rgba(255, 255, 255, 0.3);
  line-height: rem(20);
}
.playButton-box {
  width: rem(90);
  height: rem(40);
  float: right;
  padding-top: rem(5);
  box-sizing: border-box;
}
.playList img {
  border-radius: 0;
  width: rem(30);
  height: rem(30);
  float: right;
}
.playButton img {
  border-radius: 0;
  width: rem(32);
  height: rem(32);
}
.header {
  width: 100%;
  background-size: 100% auto;
  position: relative;
  transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
}
.header img {
  width: rem(30);
  height: rem(30);
  position: absolute;
  top: rem(10);
  left: rem(15);
}
.header h1 {
  width: 80%;
  overflow: hidden;
  margin: auto;
  height: rem(45);
  text-align: center;
  line-height: rem(45);
  font-size: rem(18);
}
.title-singer {
  width: 60%;
  height: rem(15);
  margin: auto;
  overflow: hidden;
  font-size: rem(15);
  text-align: center;
  margin-bottom: rem(25);
}
/*  */
.middle {
  width: 100%;
  height: 64%;
  text-align: center;
  overflow: hidden;
  position: relative;
}
.middle img {
  width: rem(300);
  height: rem(300);
  border-radius: rem(300);
  border: rem(10) solid hsla(0, 0%, 100%, 0.1);
  opacity: 1;
  transition: all 0.5s;
}
/*  */
.bottom-button-box {
  width: 100%;
  height: rem(40);
  padding: 0 rem(38);
  box-sizing: border-box;
  position: absolute;
  bottom: 7%;
  line-height: rem(40);
  display: flex;
  justify-content: space-between;
  transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
}
.bottom-button-box img {
  width: rem(33);
  height: rem(33);
  vertical-align: middle;
}
#playButton {
  width: rem(40);
  height: rem(40);
}
/*  */
.normal-enter-active,
.normal-leave-active {
  transition: all 0.4s;
}
.normal-enter {
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
}
.normal-leave-to {
  opacity: 0;
  top: 100%;
  left: -150%;
}
/*  */
@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  0% {
    transform: rotate(360deg);
  }
}
.playSrart {
  animation: rotate 20s linear infinite;
}
.playStorp {
  animation: rotate 20s linear infinite;
  -webkit-animation-play-state: paused;
}
/*  */
.time-box {
  width: 100%;
  height: rem(30);
  margin: auto;
  position: fixed;
  bottom: 12%;
  padding: rem(10) 0;
}
.time {
  width: rem(300);
  height: rem(30);
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.time span {
  display: inline-block;
  width: rem(30);
  height: rem(30);
  line-height: rem(30);
  font-size: rem(12);
}
.time-l {
  padding-right: rem(7);
}
.time-r {
  padding-left: rem(7);
}
/*  */
.dot-wrapper {
  width: 100%;
  height: rem(8);
  position: fixed;
  bottom: 22%;
  text-align: center;
}
.dot {
  display: inline-block;
  width: rem(8);
  height: rem(8);
  border-radius: rem(8);
  background: hsla(0, 0%, 100%, 0.5);
  transition: all 0.5s;
}
.active {
  width: rem(20);
  transition: all 0.5s;
  background: hsla(0, 0%, 100%, 0.8);
}
/*  */
.lyric-wrapper {
  width: 80%;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;
}
.text {
  line-height: rem(32);
  color: hsla(0, 0%, 100%, 0.5);
  font-size: rem(14);
  overflow: hidden;
}
.current {
  color: #fff;
  font-size: rem(16);
}
.middle-r {
  width: 100%;
  height: 64%;
  float: left;
  transform: translate3d(rem(375), 0, 0);
  position: absolute;
  top: 0;
}
.middleTime {
  transition: transform 0.5s;
}
/*  */
.middle .playImg {
  opacity: 0;
  transition: all 0.5s;
}
.cd-lyric {
  width: 80%;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;
  color: hsla(0, 0%, 100%, 0.589);
  font-size: rem(16);
  line-height: rem(32);
  height: rem(40);
  margin-top: 5%;
}
</style>



